                        </v-icon>
                        
                        
                    </td>
                    <td>{{ props.item.usuario }}</td>
                    <td>{{ props.item.enfermera }}</td>
                    <td>{{ props.item.tipo_historia }}</td>
                    <td>{{ props.item.serie_historia }}</td>
                    <td>{{ props.item.num_historia }}</td>
                    <td>{{ props.item.fecha_hora }}</td>
