                            </template>
                        </v-data-table>
                        <v-flex  class="text-xs-right">
                            <strong> Resultado: </strong>%{{resultado=(calculoTotal).toFixed(2)}}
                        </v-flex>


                    </v-flex>
                    <v-flex xs12 sm12 ms12 lg12 xl12>
                        <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
