</template>
<script>
    import axios from 'axios'
    import jsPDF from 'jspdf'
    import html2canvas from 'html2canvas';
    export default {
        data(){
            return {
                historias: [],
                dialog: false,
                headers: [
                
                    { text: 'Opciones', value: 'opciones', sortable: false }, 
                    { text: 'Usuario', value: 'usuario' },
                    { text: 'Enfermera', value: 'enfermera' },
                    { text: 'Tipo Historia', value: 'tipo_historia'},
                    { text: 'Serie Historia', value: 'serie_historia', sortable: false },
                    { text: 'Número Historia', value: 'num_historia', sortable: false },
                    { text: 'Fecha', value: 'fecha_hora', sortable: false },
                    { text: 'Resultado', value: 'resultado', sortable: false },
                    { text: 'Estado', value: 'estado', sortable: false }
                
                ],
                cabeceraDetalles: [
                
                    
                    { text: 'Sintoma', value: 'sintoma', sortable: false }, 
                    { text: 'valor', value: 'valor', sortable: false }
                    
                
                ],
                detalles:[
                ],
                search: '',
                id:'',
                idenfermera:'',
                enfermeras:[  
                ],
                tipo_historia:'',
                historiales: ['UCI','TRIAGE'],
                serie_historia:'',
                num_historia:'',
                codigo:'', 
                verNuevo:0,
                errorSintoma:null, 
                //ResultadoParcial:0,
                resultado:0,
                cabeceraSintomas:   [
                        {text:'Seleccionar', value: 'seleccionar', sortable: false},
                        {text:'Síntoma', value: 'sintoma', sortable: false},  
                        {text:'Enfermedad', value: 'enfermedad', sortable: false},
                        {text:'Descripción', value: 'descripcion', sortable: false},
                        {text:'Valor', value: 'valor', sortable: false}                                                                                             
                ],
                sintomas:[],  

                texto:'', 
                verSintomas:0,  
                verDet: 0,     
                valida: 0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
                adNombre:'',
                adId:'',
                inventarioModal:0,
                enfermera:'',
                fecha_hora:'',
                num_documento:'',
                direccion:'',
                telefono:'',
                email:'',
                fechaInicio:'',
                fechaFin:''
              
            }
        },
        computed: {
            calcularThal:function()    {
                var total = 0.0;
                
                 
                for(var i=0;i<this.detalles.length;i++)   {
                   if( (this.detalles[i].sintoma=='thal')  && (this.detalles[i].valor<=3) )      {
                                    total=0.10;
                             
                    }else{
                        if((this.detalles[i].sintoma=='thal')  && (this.detalles[i].valor>3 )&& (this.detalles[i].valor<=6.7 ))    {
                                    total=0.20;
                        }
                    }
                }
                return total;

            },
            calcularCa:function()    {
               // if(this.calcularTotal())    {
                 //   return;
                //}
                var total = 0.0;
                for(var i=0;i<this.detalles.length;i++)   {
                    if(  (this.detalles[i].sintoma=='enf_ca')  && (this.detalles[i].valor<=0.2) )      {
                                    total=0.10;
                                           
                    }else{
                        if((this.detalles[i].sintoma=='enf_ca')  && (this.detalles[i].valor>0.2 )&& (this.detalles[i].valor<=1.3 ))    {
                                    total=0.20;
                        }
                    }
                }
                return total;

            },
            calcularCp:function()    {
                var total = 0.0;
                
                 
                for(var i=0;i<this.detalles.length;i++)   {
                   if( (this.detalles[i].sintoma=='enf_cp')  && (this.detalles[i].valor<3.5) )      {
                                    total=0.10;
                             
                    }else{
                        if((this.detalles[i].sintoma=='enf_cp')  && (this.detalles[i].valor>=3.5 ))    {
                                    total=0.20;
                        }
                    }
                    
                }
                return total;

            },
            calcularOldpeak:function()    {
               // if(this.calcularTotal())    {
                 //   return;
                //}
                var total = 0.0;
                for(var i=0;i<this.detalles.length;i++)   {
                    if(  (this.detalles[i].sintoma=='oldpeak')  && (this.detalles[i].valor<0.55) )      {
                                    total=0.10;
                                           
                    }else{
                        if((this.detalles[i].sintoma=='oldpeak')  && (this.detalles[i].valor>=0.55 ))    {
                                    total=0.20;
                        }
                    }
                }
                return total;

            },
            calcularChol:function()    {
               // if(this.calcularTotal())    {
                 //   return;
                //}
                var total = 0.0;
                for(var i=0;i<this.detalles.length;i++)   {
                    if(  (this.detalles[i].sintoma=='chol')  && (this.detalles[i].valor<233.5) )      {
                                    total=0.10;
                                           
                    }else{
                        if((this.detalles[i].sintoma=='chol')  && (this.detalles[i].valor>=233.5 ))    {
                                    total=0.20;
                        }
                    }
                }
                return total;

            },
            calculoTotal:function()    {
               // if(this.calcularTotal())    {
                 //   return;
                //}
                var total = 0.0;
             
                if(  this.calcularThal == 0.10 && this.calcularCa== 0.10 )      {
                                    total=0.10;
                                           
                }else   {
                    if( this.calcularThal==0.10 && this.calcularCa==0.20 && this.calcularCp==0.10 ){
                                    total=0.20;
                    }else   {
                        if(this.calcularThal==0.10 && this.calcularCa==0.20 && this.calcularCp==0.20 )    {
                                    total=0.90;
                        }else{
                            if(this.calcularThal==0.20 && this.calcularOldpeak==0.20)    {
                                    total=0.90;
                            }else{
                                if(this.calcularThal==0.20 && thiss.calcularOldpeak==0.10 && this.calcularChol==0.10)    {
                                    total=0.15;
                                }else{
                                    if(this.calcularThal==0.20 && thiss.calcularOldpeak==0.10 && this.calcularChol==0.20)    {
                                        total=0.61;
                                    }
                                }
                            }
                        }
                    }
                }
                
                return total;

            }

        },

        watch: {
            dialog (val) {
            val || this.close()
            }
        },

        created () {
            this.listar();
            this.select();
            
        },
        methods:{

            mostrarSintomas()   {
                this.verSintomas=1;
            },


            crearPDF()  {
                var quotes = document.getElementById('inventario');
                html2canvas(quotes).then(function(canvas) {
                var imgData = canvas.toDataURL('image/png');
                var imgWidth= 210;
                var pageHeight = 295;
                var imgHeight = canvas.height * imgWidth / canvas.width;
                var heightLeft = imgHeight;
                var doc = new jsPDF('p','mm','a4');
                var position = 0;

                doc.addImage(imgData, 'PNG',0,position,imgWidth,imgHeight);
                doc.save('InventarioHistoria.pdf');

                });
            },

            mostrarInventario(item) {
                this.limpiar();
                this.tipo_historia=item.tipo_historia;
                this.serie_historia=item.serie_historia;
                this.num_historia=item.num_historia;
                this.enfermera=item.enfermera;
                this.num_documento=item.num_documento;
                this.direccion=item.direccion;
                this.telefono=item.telefono;
                this.email=item.email;
                this.fecha_hora=item.fecha_hora;
                this.listarDetalles(item.idhistoria);
                this.inventarioModal=1;
            },
            ocultarInventario() {
                this.inventarioModal=0;
                this.limpiar();
            },

            mostrarNuevo()  {
                this.verNuevo=1;
            },
            ocultarNuevo()  {
                this.verNuevo=0;
                this.limpiar();
            },
   
            agregarDetalle(data = [])    {
                this.errorSintoma=null;
                if(this.encuentra(data['idsintoma']))    {
                    this.errorSintoma="El sintoma ya ha sido agregado."
                }
                else    {
                    this.detalles.push(
                    {idsintoma:data['idsintoma'],
                    sintoma:data['nombre'],
                    valor:data['valor']

                    }
                );
                }
                

            },
            
            encuentra(id) {
                var sw=0;
                for(var i=0;i<this.detalles.length;i++)   {
                    if(this.detalles[i].idsintoma==id)    {
                        sw=1;
                    }

                }
                return sw;
            },
            

            listar()   {
                let me=this;
                let header= {"Authorization": "Bearer " + this.$store.state.token};
                let configuracion=  {headers : header};
                let url='';
                if(!me.fechaInicio || !me.fechaFin )    {
                    url='api/Historias/Listar';
                }
                else{
                    url='api/Historias/ConsultaFechas/'+me.fechaInicio+'/'+me.fechaFin;
                }
                 axios.get(url,configuracion).then(function(response)  {
                       //console.log(response);
                       me.historias=response.data;
                 }).catch(function(error)   {
                       console.log(error);
                 });
            },

             listarDetalles(id)   {
                let me=this;
                let header= {"Authorization": "Bearer " + this.$store.state.token};
                let configuracion=  {headers : header};
                 axios.get('api/Historias/ListarDetalles/'+id,configuracion).then(function(response)  {
                       //console.log(response);
                       me.detalles=response.data;
                 }).catch(function(error)   {
                       console.log(error);
                 });
            },

            verDetalles(item)   {
                this.limpiar();
                this.tipo_historia=item.tipo_historia;
                this.serie_historia=item.serie_historia;
                this.num_historia=item.num_historia;
                this.idenfermera=item.idenfermera;
                this.listarDetalles(item.idhistoria);
                this.verNuevo=1;
                this.verDet=1;
            },


            select()   {
                let me=this;
                var enfermerasArray=[];
                let header= {"Authorization": "Bearer " + this.$store.state.token};
                let configuracion=  {headers : header};
                
                 axios.get('api/Personas/SelectEnfermera',configuracion).then(function(response)  {
                       enfermerasArray=response.data;
                       enfermerasArray.map(function(x)    {
                           me.enfermeras.push({text:x.nombre,value:x.idpersona});
                       });
                 }).catch(function(error)   {
                       console.log(error);
                 });
            },
 
            limpiar()   {
                this.id="";
                this.idenfermera="";
                this.tipo_historia="";
                this.serie_historia="";
                this.num_historia="";
                this.codigo="";
                this.detalles=[];
                this.resultado=0;
                this.verDet=0;
                
                
            }
        }        
    }
</script>

